@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.App {
  text-align: center;
}
.nav-color {
  color: #d4e2ff !important;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.section-1 {
  background-color: #1b2242;
  color: rgb(212, 211, 211);
}
.section-2 {
  background-color: #d4e2ff;
}
.section-s-1 {
  background-color: #1b2242;
  color: rgb(212, 211, 211);
}
.section-s-2 {
  background-color: #d4e2ff;
}
.section-cf-1 {
  background-color: rgb(84, 100, 136);
  color: #d4e2ff;
}
ul {
  list-style-type: none;
}
ul li {
  display: inline;
  margin-right: 10px;
}
.s-link {
  padding: 0;
  margin-top: 20px;
  font-size: 30px;
}
.s-link-l {
  color: #d4e2ff;
}
.section-a-1 {
  background-color: rgb(45, 63, 63);
  color: #d4e2ff;
}
.section-a-2 {
  background-color: rgb(139, 116, 214);
  color: aliceblue;
  padding-top: 10px;
}
.section-a-3 {
  background-color: rgb(45, 63, 63);
  color: #d4e2ff;
}
.section-a-4 {
  background-color: rgb(139, 116, 214);
  color: aliceblue;
}

body{
    font-family: 'Roboto', sans-serif;

}

.menu-active{
    font-weight: bold !important;
    color: aquamarine !important;
    border-bottom: 1px solid antiquewhite !important;
}
.brand-name{
